"use client";

import Link from "next/link";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import {
  FileText,
  Zap,
  Shield,
  Clock,
  ArrowRight,
  CheckCircle2,
  Sparkles,
  Heart,
  Scale,
  Puzzle,
} from "lucide-react";
import { useEffect, useState } from "react";

const features = [
  {
    icon: Puzzle,
    title: "Works With Your EMR",
    description:
      "No painful migrations. No switching systems. Add AI superpowers to the EMR you already use.",
  },
  {
    icon: FileText,
    title: "Notes That Tell Your Story",
    description:
      "Generate comprehensive notes that capture the full picture of the care you provide.",
  },
  {
    icon: Shield,
    title: "Insurance-Proof Documentation",
    description:
      "Thorough, defensible notes that stand up to audits and protect your reimbursements.",
  },
  {
    icon: Clock,
    title: "Fire & Forget",
    description:
      "Start a note and move to your next patient. No waiting. No documentation backlogs.",
  },
  {
    icon: Heart,
    title: "Built for Caregivers",
    description:
      "You became a provider to help people, not to type. Get back to what matters.",
  },
  {
    icon: Scale,
    title: "HIPAA Compliant",
    description:
      "Enterprise-grade security with full encryption and audit trails. Your patients' data stays safe.",
  },
];

const valueProps = [
  {
    title: "You do great work.",
    description: "You spend real time with your patients. You listen. You care. You provide excellent care.",
  },
  {
    title: "Your notes should show it.",
    description: "But cramped documentation doesn't capture the full picture of what you do. Insurance companies use that against you.",
  },
  {
    title: "Write Great Notes changes that.",
    description: "AI-powered notes that are as thorough as the care you provide. Documentation that defends your work.",
  },
];

export default function HomePage() {
  const [showUsedTo, setShowUsedTo] = useState(false);
  const [showStrike, setShowStrike] = useState(false);

  useEffect(() => {
    const timer1 = setTimeout(() => setShowStrike(true), 1500);
    const timer2 = setTimeout(() => setShowUsedTo(true), 2000);
    return () => {
      clearTimeout(timer1);
      clearTimeout(timer2);
    };
  }, []);

  return (
    <div className="flex flex-col">
      {/* Hero Section */}
      <section className="relative overflow-hidden bg-gradient-to-b from-background to-muted/30 py-24 sm:py-36">
        {/* Subtle background pattern */}
        <div className="absolute inset-0 bg-[linear-gradient(to_right,hsl(var(--border))_1px,transparent_1px),linear-gradient(to_bottom,hsl(var(--border))_1px,transparent_1px)] bg-[size:4rem_4rem] opacity-30 [mask-image:radial-gradient(ellipse_80%_50%_at_50%_0%,#000_70%,transparent_110%)]" />
        
        <div className="container relative mx-auto px-4 sm:px-6 lg:px-8">
          <div className="mx-auto max-w-5xl text-center">
            {/* Main animated headline */}
            <h1 className="text-5xl font-black tracking-tight sm:text-7xl lg:text-8xl text-foreground mb-8">
              <span className="block mb-2">All EMRs</span>
              <span className="relative inline-block">
                {/* "used to" appears above with caret */}
                <span 
                  className={`absolute -top-8 sm:-top-12 left-1/2 -translate-x-1/2 flex flex-col items-center transition-all duration-700 ${
                    showUsedTo ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-4'
                  }`}
                >
                  <span className="text-success text-2xl sm:text-4xl font-bold whitespace-nowrap">
                    used to
                  </span>
                  <span className="text-success text-3xl sm:text-5xl leading-none">^</span>
                </span>
                {/* "Suck" with strikethrough animation */}
                <span className="relative">
                  <span className={`transition-all duration-500 ${showStrike ? 'text-muted-foreground' : 'text-destructive'}`}>
                    Suck
                  </span>
                  <span 
                    className={`absolute left-0 top-1/2 h-1 sm:h-2 bg-success transition-all duration-700 ${
                      showStrike ? 'w-full' : 'w-0'
                    }`}
                    style={{ transform: 'translateY(-50%) rotate(-2deg)' }}
                  />
                </span>
              </span>
            </h1>

            <p className="mt-12 text-xl sm:text-2xl text-muted-foreground max-w-3xl mx-auto leading-relaxed">
              Add AI-powered documentation to your{" "}
              <span className="text-foreground font-semibold">existing EMR</span>.
              <br className="hidden sm:block" />
              No migration. No learning curve. Just{" "}
              <span className="text-primary font-semibold">great notes</span>.
            </p>

            <div className="mt-12 flex flex-col sm:flex-row items-center justify-center gap-4">
              <Button 
                size="lg" 
                asChild 
                className="text-lg px-10 py-7 shadow-lg transition-all hover:scale-105"
              >
                <Link href="/signup">
                  Start Writing Great Notes
                  <ArrowRight className="ml-2 h-5 w-5" />
                </Link>
              </Button>
              <Button
                size="lg"
                variant="outline"
                asChild
                className="text-lg px-10 py-7"
              >
                <Link href="/how-it-works">
                  See How It Works
                </Link>
              </Button>
            </div>
            
            <p className="mt-6 text-sm text-muted-foreground">
              No credit card required 路 14-day free trial 路 Works with any EMR
            </p>
          </div>
        </div>

        {/* Decorative glow using brand colors */}
        <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-[800px] h-[800px] bg-primary/10 rounded-full blur-3xl pointer-events-none" />
        
        {/* Additional brand color decoration */}
        <div className="absolute inset-x-0 -top-40 -z-10 transform-gpu overflow-hidden blur-3xl sm:-top-80">
          <div
            className="relative left-[calc(50%-11rem)] aspect-[1155/678] w-[36.125rem] -translate-x-1/2 rotate-[30deg] bg-gradient-to-tr from-primary to-uranian-blue opacity-20 sm:left-[calc(50%-30rem)] sm:w-[72.1875rem]"
            style={{
              clipPath:
                "polygon(74.1% 44.1%, 100% 61.6%, 97.5% 26.9%, 85.5% 0.1%, 80.7% 2%, 72.5% 32.5%, 60.2% 62.4%, 52.4% 68.1%, 47.5% 58.3%, 45.2% 34.5%, 27.5% 76.7%, 0.1% 64.9%, 17.9% 100%, 27.6% 76.8%, 76.1% 97.7%, 74.1% 44.1%)",
            }}
          />
        </div>
      </section>

      {/* Value Proposition Section */}
      <section className="py-24 sm:py-32 bg-card">
        <div className="container mx-auto px-4 sm:px-6 lg:px-8">
          <div className="mx-auto max-w-4xl">
            <div className="text-center mb-16">
              <span className="inline-flex items-center gap-2 text-primary font-semibold text-sm uppercase tracking-wider mb-4">
                <Heart className="h-4 w-4" />
                For Healthcare Providers Who Care
              </span>
              <h2 className="text-4xl sm:text-5xl font-bold tracking-tight text-foreground">
                Your patients deserve great care.
                <br />
                <span className="text-primary">Your notes should prove it.</span>
              </h2>
            </div>

            <div className="space-y-12">
              {valueProps.map((prop, index) => (
                <div 
                  key={prop.title}
                  className={`flex flex-col md:flex-row gap-6 items-start ${
                    index % 2 === 1 ? 'md:flex-row-reverse' : ''
                  }`}
                >
                  <div className="flex-shrink-0 w-16 h-16 rounded-2xl bg-gradient-to-br from-primary to-uranian-blue flex items-center justify-center text-primary-foreground font-bold text-2xl shadow-lg">
                    {index + 1}
                  </div>
                  <div className="flex-1">
                    <h3 className="text-2xl font-bold text-foreground mb-2">{prop.title}</h3>
                    <p className="text-lg text-muted-foreground leading-relaxed">{prop.description}</p>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </section>

      {/* Problem/Solution Section */}
      <section className="py-24 sm:py-32 bg-muted/30">
        <div className="container mx-auto px-4 sm:px-6 lg:px-8">
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-16 items-center">
            <div>
              <span className="inline-flex items-center gap-2 text-destructive font-semibold text-sm uppercase tracking-wider mb-4">
                <Scale className="h-4 w-4" />
                The Problem
              </span>
              <h2 className="text-3xl sm:text-4xl font-bold text-foreground mb-6">
                Insurance companies love incomplete notes.
              </h2>
              <div className="space-y-4 text-lg text-muted-foreground">
                <p>
                  You spend 45 minutes with a patient. You take a thorough history. 
                  You do a complete exam. You provide thoughtful care.
                </p>
                <p>
                  Then you rush through documentation because you have 20 more patients to see. 
                  Your note says &ldquo;patient presents with cough, prescribed medication, follow up as needed.&rdquo;
                </p>
                <p className="text-destructive font-semibold">
                  That note doesn&apos;t defend your work. And insurance companies know it.
                </p>
              </div>
            </div>
            <div className="bg-card rounded-2xl p-8 shadow-xl border border-border">
              <span className="inline-flex items-center gap-2 text-success font-semibold text-sm uppercase tracking-wider mb-4">
                <Sparkles className="h-4 w-4" />
                The Solution
              </span>
              <h3 className="text-2xl font-bold text-foreground mb-4">
                Notes as thorough as your care.
              </h3>
              <ul className="space-y-4">
                {[
                  "Comprehensive documentation generated in seconds",
                  "Captures the full context of patient encounters",
                  "Supports medical necessity for your decisions",
                  "Stands up to audits and denials",
                  "Works alongside your existing EMR workflow",
                ].map((item) => (
                  <li key={item} className="flex items-start gap-3">
                    <CheckCircle2 className="h-6 w-6 text-success flex-shrink-0 mt-0.5" />
                    <span className="text-foreground">{item}</span>
                  </li>
                ))}
              </ul>
            </div>
          </div>
        </div>
      </section>

      {/* No Migration Section */}
      <section className="py-24 sm:py-32 bg-gradient-to-br from-primary via-jordy-blue to-uranian-blue text-primary-foreground">
        <div className="container mx-auto px-4 sm:px-6 lg:px-8">
          <div className="mx-auto max-w-4xl text-center">
            <span className="inline-flex items-center gap-2 text-primary-foreground/80 font-semibold text-sm uppercase tracking-wider mb-4">
              <Puzzle className="h-4 w-4" />
              Zero Migration Required
            </span>
            <h2 className="text-4xl sm:text-5xl font-bold mb-6">
              Keep your EMR.
              <br />
              Add superpowers.
            </h2>
            <p className="text-xl text-primary-foreground/90 mb-12 max-w-2xl mx-auto">
              We know EMR migrations are nightmares. That&apos;s why Write Great Notes 
              works <em>alongside</em> your existing system. No ripping and replacing. 
              No months of training. Just better notes, starting today.
            </p>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
              {[
                { number: "0", label: "Data migrations" },
                { number: "5 min", label: "Setup time" },
                { number: "Day 1", label: "Better notes" },
              ].map((stat) => (
                <div key={stat.label} className="bg-white/10 backdrop-blur-sm rounded-xl p-6 border border-white/20">
                  <div className="text-4xl font-black text-primary-foreground mb-2">{stat.number}</div>
                  <div className="text-primary-foreground/80">{stat.label}</div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </section>

      {/* Features Grid */}
      <section className="py-24 sm:py-32 bg-background">
        <div className="container mx-auto px-4 sm:px-6 lg:px-8">
          <div className="mx-auto max-w-2xl text-center mb-16">
            <h2 className="text-3xl font-bold tracking-tight sm:text-4xl text-foreground">
              Built for the way you actually work
            </h2>
            <p className="mt-4 text-lg text-muted-foreground">
              Documentation tools designed by healthcare providers, for healthcare providers.
            </p>
          </div>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
            {features.map((feature) => (
              <Card key={feature.title} className="border-border shadow-sm hover:shadow-md transition-all hover:-translate-y-1">
                <CardContent className="pt-6">
                  <div className="h-12 w-12 rounded-xl bg-gradient-to-br from-primary to-uranian-blue flex items-center justify-center mb-4 shadow-lg">
                    <feature.icon className="h-6 w-6 text-primary-foreground" />
                  </div>
                  <h3 className="text-lg font-semibold mb-2 text-foreground">{feature.title}</h3>
                  <p className="text-muted-foreground">{feature.description}</p>
                </CardContent>
              </Card>
            ))}
          </div>
        </div>
      </section>

      {/* CTA Section */}
      <section className="py-24 sm:py-32 bg-primary/10">
        <div className="container mx-auto px-4 sm:px-6 lg:px-8">
          <div className="mx-auto max-w-3xl text-center">
            <h2 className="text-4xl sm:text-5xl font-bold text-foreground mb-6">
              Ready to write notes that match your care?
            </h2>
            <p className="text-xl text-muted-foreground mb-10">
              You&apos;re already a great provider. Let your documentation prove it.
            </p>
            <div className="flex flex-col sm:flex-row items-center justify-center gap-4">
              <Button 
                size="lg" 
                asChild 
                className="text-lg px-10 py-7 shadow-xl transition-all hover:scale-105"
              >
                <Link href="/signup">
                  Start Free Trial
                  <ArrowRight className="ml-2 h-5 w-5" />
                </Link>
              </Button>
              <Button
                size="lg"
                variant="outline"
                asChild
                className="text-lg px-10 py-7"
              >
                <Link href="/contact">Request a Demo</Link>
              </Button>
            </div>
            <p className="mt-6 text-sm text-muted-foreground">
              No credit card required 路 Works with any EMR 路 HIPAA compliant
            </p>
          </div>
        </div>
      </section>
    </div>
  );
}
